<template>
  <div class="px-1" v-if="!this.isLoading">
    <!-- <template> -->
    <header-title class="mt-7 mb-6">
      {{ title1_text }}
    </header-title>
    <header-small-title class="mb-1">
      {{ title2_text }}
    </header-small-title>
    <v-radio-group
      v-model="selectedItem.SKU"
      hide-details
    >
      <template v-for="(option, index) in this.exchangeSizeOptions">
        <radio-item-card
          :key="index"
          :last-item="index === exchangeSizeOptions.length-1"
          :size="option"
          :selected="selectedItem.SKU === option.SKU"
          :on-click="onSizeSelected"
        />
      </template>
    </v-radio-group>
    <!-- </template> -->
    <v-row
      :class="{
        'mt-4': $vuetify.breakpoint.smAndDown,
      }"
    >
      <v-col
        :class="{
          'half-width': $vuetify.breakpoint.mdAndUp
        }"
        order-md="2"
        class="d-flex justify-end"
      >
        <action-button
          class="pr-0"
          @click.stop="togglePdpPage(true)"
          tertiary-color
          :loading="isLoading"
          :block="$vuetify.breakpoint.smAndDown"
          large
          text
        >
          {{ action2_Text }}
        </action-button>
        <PDPPage
          :visible="showPDPPage"
          @close="togglePdpPage(false)"
          :return-reason-clicked="returnReasonClicked"
        />
      </v-col>
      <v-col
        :class="{
          'half-width': $vuetify.breakpoint.mdAndUp,
        }"
      >
        <action-button
          @click.stop="onClickMethod"
          primary-color
          :loading="isLoading || isMapLoading"
          :block="$vuetify.breakpoint.smAndDown"
          large
          :disabled="isDisabled"
        >
          {{ isDisabled ? action1_Text : action1_Selected_Text }}
        </action-button>
      </v-col>
    </v-row>
  </div>
  <div v-else>
    <v-skeleton-loader type="list-item" />
  </div>
</template>

<script>
import {
  getSizeOptionsForSelectedVariant,
  saveSelectedItemToStore,
  getDefaultValue
} from '@/controllers/ExchangesController'
import { mapGetters } from 'vuex'
import { EXCHANGE_REFUND, PAGE_TITLES } from 'branding/strings.json'

export default {
  name: 'ExchangeTextView',
  components: {
    RadioItemCard: () => import('@/components/molecules/RadioItemCard.vue'),
    HeaderTitle: () => import('@/components/molecules/HeaderTitle.vue'),
    HeaderSmallTitle: () =>
      import('@/components/molecules/HeaderSmallTitle.vue'),
    PDPPage: () => import('@/components/pages/PDPPage.vue'),
    ActionButton: () => import('@/components/atoms/ActionButton.vue')
  },
  props: {
    returnReasonClicked: {
      type: Function,
      required: true
    },
    isMapLoading: {
      type: Boolean,
      required: true
    }
  },
  computed: {
    exchangeSizeOptions () {
      return getSizeOptionsForSelectedVariant(this.item?.color)
    },
    defaultValue () {
      return getDefaultValue()
    },
    item () {
      return this.selectedItems[this.currentItemIndex]
    },
    ...mapGetters({
      isLoading: 'exchange/isLoading',
      currentItemIndex: 'returns/currentItemIndex',
      selectedItems: 'order/selectedItems'
    })
  },
  data: () => ({
    showPDPPage: false,
    title1_text: EXCHANGE_REFUND.EXCHANGE_SIZE_TITLE_1,
    title2_text: EXCHANGE_REFUND.EXCHANGE_SIZE_TITLE_1,
    action1_Text: EXCHANGE_REFUND.EXCHANGE_SIZE_ACTION_TEXT_1,
    action1_Selected_Text: EXCHANGE_REFUND.EXCHANGE_SIZE_ACTION_TEXT_1_SELECTED,
    action2_Text: EXCHANGE_REFUND.EXCHANGE_SIZE_ACTION_TEXT_2,
    selectedItem: {},
    isDisabled: true
  }),
  mounted () {
    this.selectedItem = this.defaultValue
    this.updateIsDisabled()
  },
  methods: {
    onSizeSelected (selectedItem) {
      if (!selectedItem.disabled) {
        this.selectedItem = selectedItem
        this.updateIsDisabled()
      }
    },
    togglePdpPage (opening) {
      if (opening) {
        this.showPDPPage = true
        document.title = PAGE_TITLES.PDP_PAGE
      } else {
        this.showPDPPage = false
        document.title = PAGE_TITLES.EXCHANGE
      }
    },
    updateIsDisabled () {
      this.isDisabled = !(
        this.selectedItem &&
        !this.selectedItem.disabled &&
        !this.isLoading
      )
    },
    onClickMethod (event) {
      saveSelectedItemToStore(this.selectedItem)
      this.returnReasonClicked()
      this.$emit('click', event)
    }
  }
}
</script>

